<template>
  <div>
    <form novalidate class="md-layout md-alignment-center-center" @submit.prevent="validateUser">
        <md-card class="md-layout-item md-size-33 md-medium-size-50 md-small-size-100">
            <md-card-header>
                <md-card-header-text>
                    <div class="md-title"></div>
                    <div class="md-subhead">   </div>
                </md-card-header-text>
                <md-card-media md-medium style="height: unset">
                    <img :src="logoImage">
                </md-card-media>
            </md-card-header>
            <md-card-content>
                <md-field :class="getValidationClass('email')">
                    <label for="email"></label>
                    <md-input
                        v-model="form.email"
                        autocomplete="email"
                        id="email"
                        type="email"
                    />
                    <span class="md-error" v-if="!$v.form.email.required">  </span>
                    <span class="md-error" v-else-if="!$v.form.email.email">    </span>
                </md-field>
                <md-field>
                    <label></label>
                    <md-input
                    name="password"
                    id="password"
                    type="password"
                    v-model="form.password"
                    />
                    <span class="md-error" v-if="!$v.form.password.required">  </span>
                    <span class="md-error" v-else-if="!$v.form.password.minlength">8 .</span>
                </md-field>
                <div class="btn-wrapper">
                    <router-link to="/sign_up">  </router-link>
                    <md-button type="submit" class="btn-login md-raised" :md-ripple="false"></md-button>
                </div>
            </md-card-content>
        </md-card>
    </form>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import {
  required,
  email,
  minLength
} from 'vuelidate/lib/validators'

export default {
  name: 'Landing',
  mixins: [validationMixin],
  data () {
    return {
      logoImage: require('@/assets/images/logo.png'),
      form: {
        email: null,
        password: null
      }
    }
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(8)
      }
    }
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    validateUser () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.logIn()
      }
    },
    logIn () {
      this.$store.dispatch('LOGIN', this.form)
        .then((result) => {
          if (result.status === 400) {
            alert(result.data.message)
          } else {
            this.$router.push('/hospital/home')
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
form{
    background-color: #526d91;
    height: 100vh;
    .md-card{
        border-radius: 9px;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        background-color: #ffffff;
        padding: 34px 51px;

        .title{
            font-size: 35px;
            line-height: 0.57;
            color: #000000;
            margin-bottom: 3px;
        }
        .sub-title{
            font-size: 18px;
            line-height: 1.5;
            color: #1c858e;
            margin-bottom: 52px;
        }
        .btn-wrapper{
            display: flex;
            width: 100%;

            .forgot-password{
                font-size: 18px;
                line-height: 1.5;
                color: #1c858e;
                text-decoration: underline;
                cursor: pointer;
            }
            .btn-login{
                float: right;
                width: 125px;
                height: 55px;
                font-size: 20px;
                margin-left: auto;
            }
        }

        .btn-google{
            width: 100%;
            height: 57px;
            font-size: 20px;
            margin-top: 33px;
        }
    }
}
</style>



