<template>
  <div class="md-layout wrapper">
    <md-content class="md-layout-item md-xlarge-size-66 md-large-size-66 md-medium-size-100">
        <md-tabs>
            <md-tab id="tab-hospital" md-label="">
                <router-link to="/hospital/edit">
                  <md-button class="md-fab md-mini md-primary" style="float:right">
                    <md-icon>edit</md-icon>
                  </md-button>
                </router-link>
                <h1 is="sui-header" dividing>{{hospitalData.name}}</h1>
                <div class="section">
                    <sui-image-group size="small">
                        <sui-image v-for="image in hospitalImages" :key="image.display_order" :src="image.image_url" />
                    </sui-image-group>
                </div>
                <div class="section">
                  <h3 is="sui-header" dividing></h3>
                    <md-chips v-model="hashtags" md-static></md-chips>
                </div>
                <div class="section">
                  <h3 is="sui-header" dividing></h3>
                  <div class="md-layout md-gutter">
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">MON</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">TUE</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">WED</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">TUR</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">FRI</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                    <div class="md-layout-item week-wrapper">
                      <div class="week-ball">SAT</div>
                      <p>9:00</p>
                      <p>18:00</p>
                    </div>
                  </div>
                  <span class="md-body-2">SUN 09:00 ~ 12:00 / Lunch 12:00 - 13:00</span>
                </div>
                <div class="section">
                  <h3 is="sui-header" dividing></h3>
                </div>
                <sui-comment-group>
                  <h3 is="sui-header" dividing></h3>
                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/matt.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Matt</a>
                          <sui-comment-metadata>
                          <div>Today at 5:42PM</div>
                          </sui-comment-metadata>
                          <sui-comment-text>How artistic!</sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                      </sui-comment>

                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/elliot.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Elliot Fu</a>
                          <sui-comment-metadata>
                          <div>Yesterday at 12:30AM</div>
                          </sui-comment-metadata>
                          <sui-comment-text>
                          <p>This has been very useful for my research. Thanks as well!</p>
                          </sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                      <sui-comment-group>
                          <sui-comment>
                          <sui-comment-avatar src="static/images/avatar/small/jenny.jpg" />
                          <sui-comment-content>
                              <a is="sui-comment-author">Jenny Hess</a>
                              <sui-comment-metadata>
                              <div>Just now</div>
                              </sui-comment-metadata>
                              <sui-comment-text>
                              Elliot you are always so right :)
                              </sui-comment-text>
                              <sui-comment-actions>
                              <sui-comment-action>Reply</sui-comment-action>
                              </sui-comment-actions>
                          </sui-comment-content>
                          </sui-comment>
                      </sui-comment-group>
                      </sui-comment>

                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/joe.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Joe Henderson</a>
                          <sui-comment-metadata>
                          <div>5 days ago</div>
                          </sui-comment-metadata>
                          <sui-comment-text>
                          Dude, this is awesome. Thanks so much
                          </sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                  </sui-comment>
              </sui-comment-group>
            </md-tab>

            <md-tab id="tab-doctor" md-label="">
                <sui-card-group :items-per-row="2">
                    <sui-card v-for="(doctor, index) in doctors" :key="index">
                        <sui-card-content>
                          <sui-image :src="doctor.profile_img_url" class="floated doctor-img"/>
                          <sui-card-header>{{doctor.english_name}}</sui-card-header>
                          <sui-card-meta>{{doctor.korean_name}}</sui-card-meta>
                          <span class="right floated">
                            <md-button class="md-icon-button md-primary">
                              <md-icon>edit</md-icon>
                            </md-button>
                            <md-button class="md-icon-button md-accent">
                              <md-icon>close</md-icon>
                            </md-button>
                          </span>
                          <sui-card-description>
                              {{doctor.medical_subject.map((e)=>e.medical_subject.name).join()}}
                          </sui-card-description>
                        </sui-card-content>
                    </sui-card>
                    <sui-card>
                        <sui-card-content>
                          <md-button class="md-fab md-primary">
                            <md-icon>add</md-icon>
                          </md-button>
                        </sui-card-content>
                    </sui-card>
                </sui-card-group>
            </md-tab>
        </md-tabs>
    </md-content>
  </div>
</template>
<script>
export default {
  name: 'HospitalInfo',
  components: {
  },
  created () {
    // 
    this.$http.get(`${this.$api}/hospital_info`, {
      headers: {
        'X-Http-Token': this.$store.state.token
      }
    }).then((result) => {
      console.log(result)
      this.hospitalData = result.data.data.hospital
      this.hashtags = this.hospitalData.hash_tags.map(e => e.hashtag_name)
      this.hospitalImages = this.hospitalData.photos
    })

    // 
    this.$http.get(`${this.$api}/doctor`, {
      headers: {
        'X-Http-Token': this.$store.state.token,
        month: '5'
      }
    }).then((result) => {
      console.log(result)
      this.doctors = result.data.data.doctors
    })
  },
  data () {
    return {
      doctors: [],
      hospitalData: null,
      hashtags: [],
      hospitalImages: [],
      cities: [
        'New York',
        'Amsterdam',
        'Tokyo',
        'Rome'
      ]
    }
  },
  computed: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.wrapper{
  margin: 50px 30px;
  .md-content{
      margin: 0 auto;
      .section{
        margin-bottom: 40px;
      }
      .week-wrapper{
        justify-content: center;
        .week-ball{
          border-radius: 50%;
          background-color: #f8fcff;
          font-weight: bold;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 45px;
          height: 45px;
        }
      }
      .doctor-img{
          width: 80px;
          height: 80px;
          border-radius: 50%;
      }
  }
}
</style>



